"use server";

import { redirect } from "next/navigation";
import { createClient } from "../supabase/server";
import { revalidatePath } from "next/cache";

// üí° [ÏàòÏ†ï] ÏÉàÎ°ú ÌÜµÌï©Îêú helper ÌååÏùºÏóêÏÑú import
import {
  getGeminiProductFeed,
  FeedItem,
} from "@/utils/helper"; // üëà Í≤ΩÎ°ú ÏàòÏ†ï

export const githubLogin = async () => {
  const supabase = await createClient();
  const { data } = await supabase.auth.signInWithOAuth({
    provider: "github",
    options: {
      redirectTo: `${process.env.NEXT_PUBLIC_URL}/auth/callback`,
    },
  });

  if (data.url) {
    redirect(data.url); 
  }
};

export async function signOut() {
  const supabase = await createClient();
  await supabase.auth.signOut();
  revalidatePath("/");
}

/* --- üí° [Ïó¨Í∏∞Ïóê ÏÉà ÏÑúÎ≤Ñ Ïï°ÏÖò Ï∂îÍ∞Ä] --- */

const ITEMS_PER_PAGE = 5; // Ìïú Î≤àÏóê 5Í∞úÏî© Î∂àÎü¨ÏòµÎãàÎã§.

/**
 * @param page 1Î∂ÄÌÑ∞ ÏãúÏûëÌïòÎäî ÌéòÏù¥ÏßÄ Î≤àÌò∏
 */
export async function getMoreFeedItems(page: number): Promise<FeedItem[]> {
  // getGeminiProductFeedÎäî fetch Ï∫êÏãú ÎçïÎ∂ÑÏóê Ï†ÑÏ≤¥ Î™©Î°ùÏùÑ Îã§Ïãú Îã§Ïö¥Î°úÎìúÌïòÏßÄ ÏïäÏäµÎãàÎã§.
  const allItems = await getGeminiProductFeed();

  // ÏöîÏ≤≠Îêú ÌéòÏù¥ÏßÄÏóê Ìï¥ÎãπÌïòÎäî 5Í∞ú ÏïÑÏù¥ÌÖúÏùÑ ÏûòÎùº Î∞òÌôòÌï©ÎãàÎã§.
  const offset = page * ITEMS_PER_PAGE;
  return allItems.slice(offset, offset + ITEMS_PER_PAGE);
}